export function validate(values) {
  const { name, userId, password, grade, birthday, phoneNumber } = values;
  let errors = {};
  if (!name) errors['name'] = '  ';
  if (!userId) errors['userId'] = '  ';
  if (!password) errors['password'] = '   ';
  if (!grade) errors['grade'] = '  ';
  if (!birthday) errors['birthday'] = ' ';
  if (birthday && birthday.length !== 6) errors['birthday'] = '6  ex)001205';
  if (!phoneNumber) errors['phoneNumber'] = ' ';
  if (phoneNumber && phoneNumber.length !== 11)
    errors['phoneNumber'] = '  ex)01012341234';
  return errors;
}

export function publisherValidate(values) {
  const { name, userId, password, code } = values;
  let errors = {};
  if (!name) errors['name'] = '  ';
  if (!userId) errors['userId'] = '  ';
  if (!password) errors['password'] = '   ';
  if (!code) errors['code'] = '   .';
  return errors;
}
