import React, { PureComponent } from 'react';

import { Button, Dropdown, Modal, Input } from 'semantic-ui-react';

export default class LibraryFilter extends PureComponent {
  state = { open: false, folderName: '' };
  show = () => this.setState({ open: true });
  close = () => this.setState({ open: false });
  handleChange = (e, { value }) => {
    this.setState({ value });
    this.props.selectFilter(value);
  };
  handleInputChange = (e, { value }) => {
    this.setState({ folderName: value });
  };
  handleSubmit = studentId => {
    const formValue = { folderName: this.state.folderName, studentId: studentId };
    const { createFolder, requestFolderList } = this.props;
    createFolder(formValue, () => {
      this.close();
      requestFolderList({ studentId: studentId });
    });
  };
  render() {
    const { value, open } = this.state;
    const studentId = window.sessionStorage.getItem('studentId');
    const options = [
      { key: 1, text: '  ', value: 'subject' },
      { key: 2, text: ' ', value: 'folder' },
      { key: 3, text: '  ', value: 'latest' },
      { key: 4, text: '  ', value: 'alphabet' },
    ];
    return (
      <div
        style={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          paddingTop: 10,
          alignItems: 'center',
        }}
      >
        <Dropdown
          floating
          name="libraryFilter"
          onChange={this.handleChange}
          options={options}
          placeholder=" "
          value={value}
          selection
        />
        {value === 'folder' ? (
          <div style={{ paddingTop: 5 }}>
            <Button
              circular
              size="tiny"
              basic
              color="black"
              icon="add"
              content=" "
              onClick={this.show}
            />
            <Modal size="tiny" dimmer="inverted" open={open} onClose={this.close}>
              <Modal.Content>
                <p>    .</p>
                <Input fluid onChange={this.handleInputChange} placeholder="enter..." />
              </Modal.Content>
              <Modal.Actions>
                <Button negative onClick={this.close} content="" />
                <Button
                  onClick={() => this.handleSubmit(studentId)}
                  type="submit"
                  positive
                  icon="checkmark"
                  labelPosition="right"
                  content=" "
                />
              </Modal.Actions>
            </Modal>
          </div>
        ) : null}
      </div>
    );
  }
}
