import React from 'react';
import { Grid, Label, Segment, List } from 'semantic-ui-react';

export default function BookInfo(props) {
  const { book } = props;
  return (
    <Grid columns={1} rows={2}>
      <Grid.Column>
        <Segment raised>
          <Label as="a" color="red" ribbon>
            Overview
          </Label>
          <br />
          <List divided verticalAlign="middle">
            <List.Item>
              <List.Content>: {book.name}</List.Content>
            </List.Item>
            <List.Item>
              <List.Content>: {book.grade}</List.Content>
            </List.Item>
            <List.Item>
              <List.Content>: {book.subject}</List.Content>
            </List.Item>
            <List.Item>
              <List.Content>: {book.price}</List.Content>
            </List.Item>
            <List.Item>
              <List.Content>: {book.publishedDate}</List.Content>
            </List.Item>
          </List>
          <Label as="a" color="orange" ribbon>
            Introduction
          </Label>
          <br />
          <span>{book.information ? book.information : '  .'}</span>
        </Segment>
      </Grid.Column>
    </Grid>
  );
}
