import Api from '../Api';

export const FETCH_PUBLISHER_INVENTORY = 'publisher/FETCH_PUBLISHER_INVENTORY';

export const requestBookList = params => {
  return {
    type: FETCH_PUBLISHER_INVENTORY,
    promise: Api.get('/publisher/library/book', { params: params }),
    meta: {
      notification: {
        error: '    .',
      },
    },
  };
};
